import * as React from 'react';
import { reaction } from 'mobx';
import { observer, inject } from 'mobx-react';

import { Table, Progress, Pagination } from '../../../node_modules/semantic-ui-react';

import { BaseComponent } from '../components'
import { DomainStore } from '../../stores/domain.store';
import { AsyncState } from '../../models/async.models';
import { UploadPhase, UploadState } from '../../models/domain.models';
import { I18N } from '../../i18n';
import { C } from '../../c';

type Props = {
    domainStore: DomainStore
}
type State = {
    uploadState: AsyncState<UploadState[]>
    page: number
}

@inject('domainStore')
@observer
export class UploadScreen extends BaseComponent<Props, State> {

    constructor(props: Props) {
        super(props);

        this.state = {
            uploadState: props.domainStore.uploadContractsState,
            page: 1,
        }
    }

    componentDidMount() {
        document.title = I18N.common_uploadContracts

        this.disposables = [
            reaction(
                () => this.props.domainStore.uploadContractsState,
                (uploadState) => {
                    if (this.state.uploadState.isInProgress() &&
                        uploadState.isSuccessful()) {
                        const num = uploadState.value.filter(s =>
                            s.phase === 'COMPLETE').length
                        alert(I18N.uploadContract_finishMessage(num))
                    }
                    this.setState({ uploadState });
                }),
        ];
    }

    render() {
        return this.asyncStateWidget(this.state.uploadState, {
            onFail: (error) => this.asyncErrorMessage(error),
            onValue: (value) => this.renderTable(
                value.sort((s1, s2) => s1.contractTimestamp - s2.contractTimestamp)),
        });
    }

    private renderTable(states: UploadState[]) {
        const pageCount = Math.ceil(states.length / C.UPLOADT_STATES_PER_PAGE)
        const footer = pageCount > 1 ? (
            <Table.Footer>
                <Table.Row>
                    <Table.HeaderCell colSpan='8' className='right aligned'>
                        <Pagination
                            size='tiny'
                            activePage={this.state.page}
                            totalPages={pageCount}
                            nextItem={false}
                            prevItem={false}
                            siblingRange={1}
                            boundaryRange={0}
                            ellipsisItem={false}
                            onPageChange={(_, data) => this.setState({
                                page: data.activePage as number
                            })} />
                    </Table.HeaderCell>
                </Table.Row>
            </Table.Footer>
        ) : null

        const startIndex = (this.state.page - 1) * C.UPLOADT_STATES_PER_PAGE
        return (
            <Table celled size='small'>
                <Table.Header className='hideOnMobile'>
                    <Table.Row>
                        <Table.HeaderCell>{I18N.contract_timestamp}</Table.HeaderCell>
                        <Table.HeaderCell width='5'>{I18N.uploadContract_phase}</Table.HeaderCell>
                        <Table.HeaderCell width='7'>{I18N.uploadContract_progress}</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>
                <Table.Body>
                    {
                        states
                            .slice(startIndex, startIndex + C.UPLOADT_STATES_PER_PAGE)
                            .map(state => (
                                <Table.Row key={'uploadRow' + state.contractTimestamp}>
                                    <Table.Cell>{state.contractTimestamp}</Table.Cell>
                                    <Table.Cell>
                                        {I18N.syncCertificate_fromSyncPhase(state.phase)}
                                    </Table.Cell>
                                    <Table.Cell>
                                        <Progress
                                            color={state.phase == UploadPhase.AWAITS ? 'grey' : 'yellow'}
                                            success={state.phase == UploadPhase.COMPLETE}
                                            error={state.phase == UploadPhase.ERROR ||
                                                state.phase == UploadPhase.TIMEOUT_ERROR}
                                            active={
                                                state.phase == UploadPhase.PARSING_PDF ||
                                                state.phase == UploadPhase.STORING_DATA
                                            }
                                            total={4}
                                            value={
                                                state.phase == UploadPhase.AWAITS ? 0 :
                                                    state.phase == UploadPhase.UPLOADIN_PDF ? 1 :
                                                        state.phase == UploadPhase.PARSING_PDF ? 2 :
                                                            state.phase == UploadPhase.STORING_DATA ? 3 : 4
                                            }
                                        />
                                    </Table.Cell>
                                </Table.Row>
                            ))
                    }
                </Table.Body>
                {footer}
            </Table>
        )
    }
}